import React, { useContext } from "react";
import { useNavigate } from "react-router-dom"; // Para manejar la navegacin
import { CRMContext } from "../../context/CRMContext"; // Importar el contexto

const Header = () => {
    const [auth, guardarAuth] = useContext(CRMContext); // Obtenemos el estado y el mtodo para actualizarlo
    const navigate = useNavigate(); // Hook para redirigir al login

    /**
     * Maneja el cierre de sesin del usuario.
     * Limpia el token del localStorage y actualiza el contexto.
     */
    const cerrarSesion = () => {
        // Elimina el token del localStorage
        localStorage.removeItem("token");

        // Actualiza el estado de autenticacin
        guardarAuth({
            token: null,
            auth: false,
        });

        // Redirige al usuario a la pgina de Login
        navigate("/usuario");
    };

    return (
        <header className="barra">
            <div className="contenedor">
                <div className="contenido-barra">
                    <h1>Sistema Gestin De Pruebas y Control De Software</h1>

                    {/* Condicional para mostrar el botn de cerrar sesin */}
                    {auth?.auth && (
                        <button
                            type="button"
                            className="btn btn-rojo"
                            onClick={cerrarSesion} // Ejecuta la funcin de cierre de sesin al hacer clic
                        >
                            <i className="far fa-times-circle"></i>
                            Cerrar Sesin
                        </button>
                    )}
                </div>
            </div>
        </header>
    );
};

export default Header;
